import React, { useState } from 'react';
import { View, Text, StyleSheet, ScrollView, Image, TouchableOpacity, TextInput, CheckBox } from 'react-native';
import listic from "./AlwaysActive/images/listic.png";
import Filter from "./AlwaysActive/images/filter.png";
import Squ4 from "./AlwaysActive/images/sq4.png";
import arrow from "./AlwaysActive/images/arrow.png";
import { exponateList } from '../Application/Classes/testDataGenerator';  // Annahme: Diese Liste enthlt Exponate

const Exponate = () => {
    const [searchText, setSearchText] = useState('');
    const [searchByTitle, setSearchByTitle] = useState(true);
    const [showFilters, setShowFilters] = useState(false);

    // Debug: Zeige den Inhalt von exponateList
    const filteredExponate = exponateList.filter(exponat => {
        const title = exponat.title?.toLowerCase();
        const searchQuery = searchText.toLowerCase().trim();  // Entfernt Leerzeichen und ignoriert Gro-/Kleinschreibung
        if (searchByTitle && searchQuery) {
            console.log(`Comparing "${searchQuery}" with title "${title}"`);  // Debugging-Vergleich
            return title?.includes(searchQuery);
        }
        return true;
    });


    // Debug: Zeige die gefilterte Liste



    return (
        <View style={styles.container}>
            <View style={{ flexDirection: "row" }}>
                <Text style={styles.welcomeText}>Exponate</Text>

                <View style={styles.searchContainer}>

                    <View style={{
                        borderWidth: 1,
                        borderColor: 'gray',
                        borderRadius: 5,
                        flexDirection: "row",
                        width: 467.5,
                    }}>
                        <TextInput
                            style={styles.searchBar}
                            placeholder="Search Exponate"
                            value={searchText}
                            onChangeText={text => setSearchText(text)}
                        />
                        <TouchableOpacity style={styles.filterButton} onPress={() => setShowFilters(!showFilters)}>
                            <Image style={{ width: 20, height: 20 }} source={Filter} />
                        </TouchableOpacity>
                    </View>
                </View>
            </View>

            {showFilters && (
                <View style={styles.dropdown}>
                    <View style={styles.checkBoxItem}>
                        <CheckBox
                            value={searchByTitle}
                            onValueChange={() => {
                                setSearchByTitle(!searchByTitle);
                            }}
                        />
                        <Text> Search by Title</Text>
                    </View>
                </View>
            )}
            <View style={{margin:10,flexDirection:"row"}}>
                <Text style={[styles.listValueName,{width:300}]}>Exponate-ID</Text>
                <Text style={[styles.listValueName,{width:300}]}>Titel</Text>
                <Text style={[styles.listValueName,{width:300}]}>Kategorie</Text>
                <Text style={[styles.listValueName,{width:1000}]}>Gewicht</Text>

            </View>
            <ScrollView contentContainerStyle={styles.scrollViewContent}>

                    <View style={styles.simpleList}>
                        {filteredExponate.length > 0 ? (
                            filteredExponate.map((exponat, index) => (
                                <View style={{ width: 1300, flexDirection: "row" }} key={index}>
                                    <View style={styles.simpleListItem}>
                                        <View style={{ flexDirection: "row" }}>
                                            <Text style={{ fontWeight: "bold",width:300 }}>{exponat.exponatId}</Text>
                                            <Text style={{ width:300 }}>{exponat.titel}</Text>
                                            <Text style={{ width:300 }}>{exponat.kategorie}</Text>
                                            <Text style={{ width:1000 }}>{exponat.gewicht}</Text>

                                        </View>
                                        <View>
                                            <TouchableOpacity>
                                                <Image style={{ width: 25, height: 25 }} source={arrow} />
                                            </TouchableOpacity>
                                        </View>
                                    </View>
                                </View>
                            ))
                        ) : (
                            <Text>No Exponate found</Text>
                        )}
                    </View>
                )
            </ScrollView>
        </View>
    );
};

